
var helper = require('Helper');

cc.Class({
    extends: cc.Component,

    properties: {
        username: cc.Label,
        redPlay:  cc.Label,
        redWin:   cc.Label,
        redLost:  cc.Label,
        redLai:   cc.Label,
        LastTime: cc.Label,
        LastBet:  cc.Label,
    },
    setData: function(data) {
        this.username.string = void 0 !== data.name ? data.name : "";
        this.redPlay.string  = helper.numberWithCommas(data.bet);
        this.redWin.string   = helper.numberWithCommas(data.win);
        this.redLost.string  = helper.numberWithCommas(data.lost);
        this.redLai.string   = helper.numberWithCommas(data.totall);
        this.LastTime.string = void 0 !== data.time ? helper.getStringDateByTime(data.time) : "";
        this.LastBet.string  = void 0 !== data.select ? helper.numberWithCommas(data.select) : "";
    },
});
