cc.Class({
	extends: cc.Component,

	properties: {
		Begin_D: cc.EditBox,
		Begin_M: cc.EditBox,
		Begin_Y: cc.EditBox,
		Day: cc.EditBox,
		Member: cc.EditBox,
		Top1: cc.EditBox,
		Top2: cc.EditBox,
		Top3: cc.EditBox,
		Top4: cc.EditBox,
		Top5: cc.EditBox,
		Top6_10: cc.EditBox,
		Top11_20: cc.EditBox,
		Top21_50: cc.EditBox,
		Top51_xxx: cc.EditBox,

		status: {
			default: [],
			type: cc.Toggle,
		},
	},
	onEnable: function () {
		cc.RedT.send({eventvip:{setupEvent:true}});
	},
	onData: function(data){
		this.Begin_D.string   = data.begin_d;
		this.Begin_M.string   = data.begin_m;
		this.Begin_Y.string   = data.begin_y;
		this.Day.string       = data.day;
		this.Member.string    = data.member;
		this.Top1.string      = data.top1;
		this.Top2.string      = data.top2;
		this.Top3.string      = data.top3;
		this.Top4.string      = data.top4;
		this.Top5.string      = data.top5;
		this.Top6_10.string   = data.top6_10;
		this.Top11_20.string  = data.top11_20;
		this.Top21_50.string  = data.top21_50;
		this.Top51_xxx.string = data.top51_xxx;
		if (data.status) {
			this.status[0].isChecked = false;
			this.status[1].isChecked = true;
		}else{
			this.status[0].isChecked = true;
			this.status[1].isChecked = false;
		}
	},
	onClickSave: function(){
		cc.RedT.send({eventvip:{update: {
			status:  this.status[1].isChecked,
			begin_d: this.Begin_D.string,
			begin_m: this.Begin_M.string,
			begin_y: this.Begin_Y.string,
			day: this.Day.string,
			member: this.Member.string,
			top1: this.Top1.string,
			top2: this.Top2.string,
			top3: this.Top3.string,
			top4: this.Top4.string,
			top5: this.Top5.string,
			top6_10: this.Top6_10.string,
			top11_20: this.Top11_20.string,
			top21_50: this.Top21_50.string,
			top51_xxx: this.Top51_xxx.string,
		}}});
	}
});
